<template>
  <button 
    :class="[ selected ? 'selected' : 'not-selected', hover ? 'hover' : '' ]"
    @mouseover="hover = true"
    @mouseleave="hover = false"
    @click="$emit('change-category', name)"
    >
    {{name}}
  </button>
</template>

<script>
export default {
  name: 'MenuButton',
  props: {
    name: String,
    selected: Boolean,
  },
  data() {
    return {
      hover: false
    }
  },
}
</script>

